.rtrb-components-popover,
.rtrb-panel-control-wrapper {
	.rtrb-control-field {
		&.rtrb-cf-background-wrap {
			.rtrb-cf-body-btn-wrap {
				display: flex;
				align-items: center;
				gap: 6px;
				margin-bottom: 14px;
				.rtrb-btn-group {
					flex-grow: 1;
				}
				.rtrb-undo-btn {
					padding: 0;
					background-color: transparent;
					&:hover {
						background-color: transparent;
					}
					&:focus,
					&:hover,
					&:active {
						outline: 0;
						box-shadow: none;
					}
				}
			}
		}
	}
}
