.rtcl-slider-wrapper {
	.swiper-wrapper {
		height: inherit;
	}

	.rtcl-slider {
		.rtcl-lightbox-iframe {
			width: 100%;
			height: 400px;
		}

		.swiper-slide {
			img {
				width: 100%;
				height: 400px;
			}
		}
	}

	.rtcl-slider-nav {
		.swiper-wrapper {
			transform: none !important;
		}

		.swiper-slide {
			width: 172px;
			height: 92px;
			text-align: left;
			overflow: hidden;

			img {
				width: 100%;
				height: 100%;
			}
		}
	}

	.rtcl-slider .rtcl-listing-gallery__trigger {
		align-items: center;
		border-radius: 100%;
		box-sizing: content-box;
		display: flex;
		font-size: 14px;
		justify-content: center;
		position: absolute;
		right: 0.5em;
		top: 0.5em;
		width: 30px;
		height: 30px;
		background-color: #e43d40;
		color: #ffffff;
		z-index: 9;
	}
}