.rtrb-components-popover,
.rtrb-panel-control-wrapper {
	.rtrb-control-field {
		&.rtrb-cf-typography-wrap {
			display: flex;
			align-items: center;
			justify-content: space-between;

			.components-button {
				font-size: var(--rtrb-admin-text-base, 14px);
				background-color: #e0e0e0;
				height: 28px !important;
				width: 28px !important;
				padding: 0;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				> span {
					transition: unset;
				}
				&:hover {
					color: var(--rtrb-admin-color-white, #fff);
					background-color: var(--rtrb-admin-color-primary, #0066ff);
				}
			}
		}
	}
	.rtrb-typography-content {
		min-width: 230px;
		padding: 5px 6px;
	}
}
