.rtcl-block-editor {
	.rtcl.store-content-wrap {
		padding: 0;

		.store-banner {
			margin: 0;

			.banner {
				background-size: cover;
				max-height: none;

				img {
					height: 100%;
					-o-object-fit: cover;
					object-fit: cover;
					-o-object-position: bottom;
					object-position: bottom;
					position: absolute;
				}
			}
		}

	}

}