.rtrb-panel-control-wrapper {
	.rtrb-control-field {
		&.rtrb-cf-styles-wrap {
			margin-bottom: 16px !important;

			.rtrb-style-list {
				--row-gap: 10px;
				--column-gap: 5px;

				display: grid;
				row-gap: var(--row-gap);
				column-gap: var(--column-gap);

				>div {
					--row-gap: 6px;
					--column-gap: 6px;
					display: grid;
					row-gap: var(--row-gap);
					column-gap: var(--column-gap);
					text-align: center;
					cursor: pointer;
					overflow: hidden;

					.rtrb-layout {
						display: block;
						border: 3px solid var(--rtrb-admin-color-border);
						border-radius: 0;
						transition: var(--rtrb-admin-transition);
						overflow: hidden;

						img {
							display: block;
							width: 100%;
						}
					}

					.rtrb-style-icon,
					.rtrb-style-svg,
					.rtrb-label {
						display: block;
					}

					.rtrb-label {
						font-size: 12px !important;
						line-height: 1.2;
						transition: var(--rtrb-admin-transition);
					}

					&.rtrb-active {
						.rtrb-layout {
							border-color: var(--rtrb-admin-color-primary);
						}

						.rtrb-label {
							color: var(--rtrb-admin-color-primary) !important;
						}
					}
				}

				&.rtrb-style-columns-2 {
					grid-template-columns: repeat(2, 1fr);
				}

				&.rtrb-style-columns-3 {
					grid-template-columns: repeat(3, 1fr);
				}

				&.rtrb-style-columns-4 {
					grid-template-columns: repeat(4, 1fr);
				}

				.is-pro {
					position: relative;

					.rtrb-layout {
						position: relative;

						&::after {
							background: rgba(255, 255, 255, 0.4);
							bottom: 0;
							content: "";
							left: 0;
							position: absolute;
							right: 0;
							top: 0;
							z-index: 1;
						}

						&::before {
							content: "PRO";
							font-family: Roboto, Arial, Helvetica, Verdana, sans-serif;
							position: absolute;
							top: 3px;
							left: -40px;
							color: #fff;
							z-index: 100;
							width: 100px;
							height: 15px;
							line-height: 16px;
							text-align: center;
							background: #4360ef;
							font-weight: 500;
							-webkit-transform: rotate(-45deg);
							transform: rotate(-45deg);
							font-size: 9px;
						}
					}

					.rtrb-pro-lable {
						background: #ff002b;
						color: #fff;
						display: inline-block;
						font-size: 10px;
						font-weight: 600;
						line-height: 1;
						padding: 2px 4px 3px;
						position: absolute;
						right: 0;
						text-transform: uppercase;
						top: 0;
						z-index: 99;
						border-radius: 2px;
					}

					&.rtrb-pro {
						-webkit-animation: rtsbpro-shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
						animation: rtsbpro-shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
						-webkit-transform: translate3d(0, 0, 0);
						transform: translate3d(0, 0, 0);
						-webkit-backface-visibility: hidden;
						backface-visibility: hidden;
						-webkit-perspective: 1000px;
						perspective: 1000px;
					}

					@keyframes rtsbpro-shake {

						10%,
						90% {
							transform: translate3d(-1px, 0, 0);
						}

						20%,
						80% {
							transform: translate3d(2px, 0, 0);
						}

						30%,
						50%,
						70% {
							transform: translate3d(-4px, 0, 0);
						}

						40%,
						60% {
							transform: translate3d(4px, 0, 0);
						}
					}
				}
			}
		}
	}
}