.rtcl-widget-filter-class {
	.panel-block {
		.filter-list {
			.arrow {
				bottom: inherit;
				left: inherit;
				width: inherit;
				height: inherit;
				&::after {
					content: none;
				}
			}
		}
	}
}

.rtcl-widget-filter-class
	.panel-block
	.rtcl-filter-form
	.ui-accordion-item.is-open
	.ui-accordion-content {
	ul {
		list-style: none;
		padding: 0;
		margin: 0;
	}
}
