.rtcl-single-block {
	&.classima-listing-single
		.classima-single-details
		.rtin-specs
		.rtin-spec-items {
		list-style: none;
		padding: 0;
		margin: 0;
		li {
			padding-left: 30px;
			position: relative;
		}
	}

	&.classima-listing-single
		.classima-single-details
		.rtin-specs
		.rtin-spec-items
		li:before {
		background-color: #f85c70;
	}

	&.classima-listing-single
		.classima-single-details
		.rtin-specs
		.rtin-spec-items
		li:before {
		text-align: center;
		content: "\f345";
		height: 16px;
		width: 16px;
		position: absolute;
		font-family: "dashicons";
		font-weight: 600;
		font-size: 15px;
		line-height: 1;
		left: 0;
		top: 5px;
		border-radius: 50%;
		z-index: 1;
		color: #ffffff;
		background-color: #f85c70;
	}
}
